import React from "react";
import {
  Box,
  Container,
  Typography,
  Grid,
  Stack,
  Breadcrumbs,
  Link,
  Paper,
  Divider
} from "@mui/material";

import NavigateNextIcon from "@mui/icons-material/NavigateNext";

import SecurityIcon from "@mui/icons-material/Security";
import VerifiedIcon from "@mui/icons-material/Verified";
import SpaIcon from "@mui/icons-material/Spa";

/* Brand Colors */
const GOLD = "#cc9840";
const DARK_BLUE = "#193968";

/* SERVICES */
const offerings = [
  { icon: "https://unibcompoldecom.s3.ap-south-1.amazonaws.com/1770790897975/1770790897975_photo_2024-09-05_16-54-17-4-1.webp", label: "AEPS" },
  { icon: "https://unibcompoldecom.s3.ap-south-1.amazonaws.com/1770790522503/1770790522503_booking.png", label: "Flight Booking" },
  { icon: "https://unibcompoldecom.s3.ap-south-1.amazonaws.com/1770790399900/1770790399900_671495.png", label: "Money Transfer" },
  { icon: "https://unibcompoldecom.s3.ap-south-1.amazonaws.com/1770790249393/1770790249393_bbpsn.png", label: "BBPS" },
  { icon: "https://unibcompoldecom.s3.ap-south-1.amazonaws.com/1770791218198/1770791218198_recharge.png", label: "Mobile Recharge" },
  { icon: "https://unibcompoldecom.s3.ap-south-1.amazonaws.com/1770791127890/1770791127890_insurance.png", label: "Insurance" },
  { icon: "https://unibcompoldecom.s3.ap-south-1.amazonaws.com/1770791442758/1770791442758_videos.png", label: "OTT Topup" },
  { icon: "https://unibcompoldecom.s3.ap-south-1.amazonaws.com/1770791760825/1770791760825_payment.png", label: "Credit Card Payment" },
  { icon: "https://unibcompoldecom.s3.ap-south-1.amazonaws.com/1770792172664/1770792172664_hotel.png", label: "Hotel" },
  { icon: "https://unibcompoldecom.s3.ap-south-1.amazonaws.com/1770792226591/1770792226591_gas-meter.png", label: "Piped Gas" },
  { icon: "https://unibcompoldecom.s3.ap-south-1.amazonaws.com/1770792360482/1770792360482_rail-ticket-booking-irctc-logo-in-11563999077stkayrwu17.png", label: "IRCTC Booking" },
  { icon: "https://cdn-icons-png.flaticon.com/512/1828/1828919.png", label: "More..." }
];


const ServicesPage = () => {
  return (
    <Box sx={{ bgcolor: "#f5f7fa" }}>

      {/* ===== HERO ===== */}
      <Box
        sx={{
          background:
            "linear-gradient(135deg, rgb(23,59,75) 0%, #23627e 100%)"
        }}
      >
        <Container maxWidth="lg" sx={{ py: { xs: 6, md: 10 } }}>

          <Breadcrumbs
            separator={<NavigateNextIcon sx={{ color: "rgba(255,255,255,0.7)" }} />}
            sx={{ mb: 3 }}
          >
            <Link href="/" sx={{ color: "rgba(255,255,255,0.9)", textDecoration: "none" }}>
              Home
            </Link>
            <Typography color="white">Services</Typography>
          </Breadcrumbs>

          <Typography variant="h2" color="white" fontWeight={700}>
            Our Services
          </Typography>

          <Typography sx={{ mt: 2, color: "rgba(255,255,255,0.9)" }}>
            Explore our secure and reliable financial service ecosystem.
          </Typography>

        </Container>
      </Box>

      {/* ===== CONTENT ===== */}
      <Container maxWidth="lg" sx={{ py: 8 }}>

        <Grid container spacing={6}>

          {/* PROMISE */}
          <Grid item xs={12} md={4}>

            <Typography variant="h4" fontWeight={700} mb={4} color={DARK_BLUE}>
              Our Promise
            </Typography>

            <Stack spacing={3}>

              {[
                {
                  icon: <VerifiedIcon />,
                  title: "Quality",
                  desc: "Uninterrupted delivery of services."
                },
                {
                  icon: <SpaIcon />,
                  title: "Eco-Minded",
                  desc: "Minimal carbon footprint."
                },
                {
                  icon: <SecurityIcon />,
                  title: "Assured Safety",
                  desc: "Strict compliance & security standards."
                }
              ].map((item, index) => (
                <Paper
                  key={index}
                  elevation={0}
                  sx={{
                    p: 2.5,
                    borderRadius: 3,
                    border: "1px solid #e6eaf0",
                    display: "flex",
                    gap: 2,
                    transition: ".3s",
                    "&:hover": {
                      borderColor: GOLD,
                      boxShadow: "0 10px 25px rgba(0,0,0,0.05)"
                    }
                  }}
                >
                  <Box
                    sx={{
                      width: 48,
                      height: 48,
                      borderRadius: 2,
                      bgcolor: `${GOLD}20`,
                      color: GOLD,
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "center"
                    }}
                  >
                    {item.icon}
                  </Box>

                  <Box>
                    <Typography fontWeight={700}>
                      {item.title}
                    </Typography>

                    <Typography variant="body2" color="text.secondary">
                      {item.desc}
                    </Typography>
                  </Box>
                </Paper>
              ))}

            </Stack>
          </Grid>

          <Grid item xs={12} md={8}>

            <Typography variant="h4" fontWeight={700} mb={4} color={DARK_BLUE}>
              Our Offerings
            </Typography>

            <Grid container spacing={4}>

              {offerings.map((item, index) => (
                <Grid item xs={6} sm={4} md={3} key={index}>

                  <Stack
                    alignItems="center"
                    spacing={1.5}
                    sx={{
                      p: 2,
                      borderRadius: 3,
                      transition: ".3s",
                      cursor: "pointer",
                      "&:hover": {
                        transform: "translateY(-6px)",
                        bgcolor: "white",
                        boxShadow: "0 15px 35px rgba(0,0,0,0.08)"
                      }
                    }}
                  >

                    <Box
  sx={{
    width: 70,
    height: 70,
    borderRadius: 3,
    bgcolor: `${GOLD}15`,
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    p: 1.5
  }}
>
  <img
    src={item.icon}
    alt={item.label}
    style={{
      width: "40px",
      height: "40px",
      objectFit: "contain"
    }}
  />
</Box>


                    <Typography
                      fontWeight={600}
                      textAlign="center"
                      color={DARK_BLUE}
                    >
                      {item.label}
                    </Typography>

                  </Stack>

                </Grid>
              ))}

            </Grid>

          </Grid>

        </Grid>

      </Container>

      <Container maxWidth="lg" sx={{ pb: 6 }}>
        <Paper
          elevation={0}
          sx={{
            p: 3,
            textAlign: "center",
            borderRadius: 3,
            bgcolor: "grey.50",
            border: "1px solid",
            borderColor: "divider"
          }}
        >
          <Typography variant="body2" color="text.secondary" sx={{ lineHeight: 1.8 }}>
            For questions or clarifications regarding BankMudraa services,
            please contact our support team.
          </Typography>

          <Divider sx={{ my: 2 }} />

          <Typography variant="caption" color="text.secondary">
            © 2025-2026 BankMudraa. All rights reserved |
            Techmoney Solutions Private Limited
          </Typography>
        </Paper>
      </Container>

    </Box>
  );
};

export default ServicesPage;
